export default {
    layout: 'Adm-content',
    data() {
        return {
            students: '',
            search_item: '',
        }
    },
    methods: {
        studentSearch() {
            this.$axios
                .$get("/admission/search-student/" + this.search_item)
                .then((response) => {
                    this.students = response;
                })
                .catch((error) => {
                    this.$toaster.error("Student Not Found");
                });
        }
    }
}
